.skills {
    padding: 50px 100px 50px 100px;
    min-height: 100vh;
    width: 100%;

    position: relative;

    background-color: black;

    display: flex;
    // align-items: center;
    // justify-content: center;
    flex-direction: column;
    gap: 50px;

    &__title {
        font-size: 5rem;
        justify-self: flex-start;
    }

    &__categories {
        justify-self: center;
        width: 100%;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        gap: 50px;
        width: 100%;
    }

    &__web {
        width: 100%;

        display: flex;
        flex-direction: column;
    }

    &__artist {

        display: flex;
        flex-direction: column;
    }

    &__sub-title {
        font-size: 2rem;
        margin-bottom: 10px;
    }

    &__circles {
        display: flex;
        gap: 10px;
        width: 100%;
        flex-wrap: wrap;
    }
}

@media only screen and (max-width: 500px) {
    .skills {
        padding: 50px 100px 100px 100px;
        height: unset;
    
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: 50px;
    
        &__title {
            font-size: 5rem;
            justify-self: flex-start;
        }
    
        &__categories {
            justify-self: center;
            width: 100%;
            flex-grow: 1;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            gap: 50px;
        }
    
        &__web {
            width: 100%;
            position: relative;
    
            display: flex;
            flex-direction: column;
        }
    
        &__artist {
            position: relative;
    
            display: flex;
            flex-direction: column;
        }
    
        &__sub-title {
            font-size: 2rem;
            margin-bottom: 10px;
            text-align: center;
        }
    
        &__circles {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 20px;
        }
    }
}